import React, {useEffect, useState} from "react"
import {
    useParams
} from "react-router-dom";
import axios from "../apis/axios";
import labelTranslate from "../components/labelTranslate"
import urlTable from "../Constant/urlTable";

// urlTableShow

const Show = () => {
    //ToDo Add pageStatus condition
    const [pageStatus, setPageStatus] = useState("");
    const [data, setData] = useState([]);
    const dataString = []
    const {model, id} = useParams();
    useEffect(() => {
        const fetchData = async () => {
            try {
                const result = await axios.get(`${urlTable[model]}/${id}/show`);
                if (result.status === 200) {
                    setData(result.data)
                    setPageStatus("successful");

                }
            } catch (error) {
                setPageStatus("error")
            }
        }
        fetchData()
    }, [model])

    for (const i in data) {
        if (typeof data[i] === "string") {
            dataString.push(
                {
                    label: labelTranslate(i),
                    value: data[i],
                }
            )
        } else if (typeof data[i] === "number") {
            dataString.push(
                {
                    label: labelTranslate(i),
                    value: data[i].toString(),
                }
            )
        } else if (typeof data[i] === "object") {
            if(! i.includes('_many')){
                let buffer = "";
                for (const item of data[i]) {
                    buffer += (" , " + item.f_title);
                }
                dataString.push(
                    {
                        label: labelTranslate(i),
                        value: buffer.slice(2, buffer.length),
                    }
                );
            }
        }

    }

    return (
        <React.Fragment>
            <h1>
                Show {model} {id}
            </h1>
            {
                dataString.map(
                    (row, index) => {
                        let value = (<React.Fragment/>)
                        if (row.value.indexOf("http") === -1) {
                            value = (<p key={index}>{row.value}</p>)
                        } else {
                            const extension = row.value.slice(-3);
                            switch (extension) {
                                case "png":
                                case "jpg":
                                    value = (<img key={index} src={row.value} className="img-thumbnail" alt="..."/>);
                                    break
                                case "mp4":
                                    value = (
                                        <div key={index} className="embed-responsive embed-responsive-16by9">
                                            <video controls>
                                                <source src={row.value} type="video/mp4"/>
                                                Sorry, your browser doesn't support embedded videos.
                                            </video>
                                        </div>
                                    )
                                    break;
                                case "avi":
                                    value = (
                                        <div key={index} className="embed-responsive embed-responsive-16by9">
                                            <video controls>
                                                <source src={row.value} type="video/avi"/>
                                                Sorry, your browser doesn't support embedded videos.
                                            </video>
                                        </div>
                                    )
                                    break;
                                default:
                                    value =
                                        (
                                            <h4 key={index}>فرمت این فایل پشتیبانی نمی شود</h4>
                                        )
                            }
                        }
                        return (
                            <div key={index} className="row">
                                <div className="col-2">
                                    <p>{row.label}</p>
                                </div>
                                <div className="col-10">
                                    {value}
                                </div>
                            </div>
                        )
                    }
                )
            }
        </React.Fragment>
    )
}
export default Show;