import React, {useEffect, useState} from "react"
import Footer from "../components/Footer";
import "../styles/customTheme.styles.scss";
import "../style.css"
import CustomNavbar from "../components/custom-navbar/custom-navbar";
import { useParams} from "react-router-dom";
import axios from "../apis/axios";
import Wait from "../components/Wait";
import {Tab, TabList, TabPanel, Tabs} from "react-tabs";
import 'react-tabs/style/react-tabs.css';
import moment from 'jalali-moment'

const navColor = "#5a5a5a";
const Departmant = () => {
    let {model} = useParams();
    useEffect(() => {
        const fetchData = async () => {
            try {
                const result = await axios.get(`comidt/service/project/${model}/show`);
                if (result.status === 200) {
                    setProject(result.data)
                    console.log(result.data)
                    setPageStatus("successful");

                }
            } catch (error) {
                setPageStatus("error")
            }
        }
        fetchData()
    }, [])
    const oathBackground = "#ffffff";
    const [project, setProject] = useState({})
    const [pageStatus, setPageStatus] = useState("load")


    switch (pageStatus) {
        case "load":
            <Wait/>
        case "error":
            return (
                <React.Fragment>
                    <div className="container">
                        <CustomNavbar nameClass={navColor}/>
                    </div>

                    <div className="l-container" style={{background: "#F7F7F7"}}>
                        <div className="d-flex justify-content-center">
                            <h1>
                                پروژه پیدا نشد
                            </h1>
                        </div>
                    </div>
                    <Footer/>
                </React.Fragment>
            )
        case "successful":
            return (
                <React.Fragment>
                    <div className="container">
                        <CustomNavbar nameClass={navColor}/>
                    </div>

                    <div className="l-container" style={{background: "#F7F7F7"}}>
                        <div className="container">
                            <div className="mr-4 row">
                                <div className="col-lg-8">
                                    <div className="card" style={{background: "white"}}>
                                        <img className="card-img-top" src={project.f_project_image}
                                             alt="Card image cap"/>
                                        <div className="card-body">
                                            <h5 className="card-title text-fosfori">توضیح مختصر درباره پروژه</h5>
                                            <p className="card-text">
                                                {project.f_description}
                                            </p>
                                        </div>
                                    </div>
                                    <h5 className="text-fosfori my-5">آلبوم عکس</h5>
                                    <div className="w-100" style={{background: "white"}}>
                                        <Tabs>
                                            <TabList>
                                                <Tab>نقشه</Tab>
                                                <Tab>پرسپکتیو</Tab>
                                                <Tab>مراحل اجرا</Tab>
                                            </TabList>
                                            <TabPanel>
                                                {
                                                    project.f_map.map(
                                                        (image, index) => {
                                                            return (
                                                                <img key={index} className="img-thumbnail m-2"
                                                                     src={image.f_file} alt="map"/>
                                                            )
                                                        }
                                                    )
                                                }
                                            </TabPanel>
                                            <TabPanel>
                                                {
                                                    project.f_perspective.map(
                                                        (image, index) => {
                                                            return (
                                                                <img key={index} className="img-thumbnail m-2"
                                                                     src={image.f_file} alt="map"/>
                                                            )
                                                        }
                                                    )
                                                }
                                            </TabPanel>
                                            <TabPanel>
                                                {
                                                    project.f_execution_step.map(
                                                        (image, index) => {
                                                            return (
                                                                <img key={index} className="img-thumbnail m-2"
                                                                     src={image.f_file} alt="map"/>
                                                            )
                                                        }
                                                    )
                                                }
                                            </TabPanel>
                                        </Tabs>
                                    </div>
                                    <h5 className="text-fosfori my-5">کلیپ</h5>
                                    <div className="w-100" style={{background: "white"}}>
                                        <div className="p-2 py-5 d-flex justify-content-center">
                                            <img src="/images/viedo.png" alt="pic"
                                                 className="img-responsive"/>
                                        </div>
                                    </div>
                                </div>
                                <div className="col-lg-4">
                                    <div className="bg-white sticky-top ">
                                        <div className="pr-3">
                                            <h5 className="text-fosfori pt-3">رو تیتر</h5>
                                            <h1 className="pb-3">
                                                {project.f_title}
                                            </h1>
                                        </div>
                                        <div className="card" style={{background: "white"}}>
                                            <img className="card-img-top" src={project.f_project_image}
                                                 alt="Card image cap"/>
                                        </div>
                                        <div className="row">
                                            <div className="col-4">
                                                <div>
                                                    کارفرما
                                                </div>
                                                <div>
                                                    <h5 className="text-fosfori py-2">{project.f_employer}</h5>

                                                </div>
                                            </div>
                                            <div className="col-4">
                                                <div>
                                                    مشاور
                                                </div>
                                                <div>
                                                    <h5 className="text-fosfori py-2">{project.f_consultant}</h5>

                                                </div>
                                            </div>
                                            <div className="col-4">
                                                <div>
                                                    محل اجرا
                                                </div>
                                                <div>
                                                    <h5 className="text-fosfori py-2">{project.f_place}</h5>

                                                </div>
                                            </div>
                                            <div className="col-4">
                                                <div>
                                                    زمان شروع
                                                </div>
                                                <div>
                                                    {/*<h5 className="text-fosfori py-2">{project.f_start_time}</h5>*/}
                                                    <h5 className="text-fosfori py-2">{moment(project.f_start_time, 'YYYY/MM/DD').locale('fa').format('YYYY/MM/DD')}</h5>
                                                </div>
                                            </div>
                                            <div className="col-4">
                                                <div>
                                                    مدت قرارداد
                                                </div>
                                                <div>
                                                    <h5 className="text-fosfori py-2">{project.f_duration}</h5>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <Footer/>
                </React.Fragment>
            );

    }


}
export default Departmant;