import React, {useEffect, useState} from "react"
import Footer from "../components/Footer";
import "../styles/customTheme.styles.scss";
import "../style.css"
import CustomNavbar from "../components/custom-navbar/custom-navbar";
import {useParams} from "react-router-dom";
import axios from "../apis/axios";
import Wait from "../components/Wait";

const navColor = "#5a5a5a";
const Departmant = () => {
    let {model} = useParams();
    useEffect(() => {
        const fetchData = async () => {
            try {
                // const result = await axios.get("comidt/departments"); http://comidt.ir:8000/
                const result = await axios.get(`comidt/service/department/${model}/show`);
                if (result.status === 200) {
                    setPageStatus("successful");

                    Object.keys(result.data).map(
                        (key, index) => {
                            if (typeof (result.data[key]) === "string") {
                                result.data[key] = result.data[key].includes("http://comidt.ir:8000") ? result.data[key].slice(21) : result.data[key];
                                result.data[key] = result.data[key].includes("http://127.0.0.1:8000") ? result.data[key].slice(21) : result.data[key];
                                result.data[key] = result.data[key].includes("http://127.0.0.1:3000") ? result.data[key].slice(21) : result.data[key];
                                result.data[key] = result.data[key].includes("http://localhost:3000") ? result.data[key].slice(21) : result.data[key];
                                result.data[key] = result.data[key].includes("http://localhost:8000") ? result.data[key].slice(21) : result.data[key];
                            }
                        }
                    );
                    setDepartmant(result.data);
                    console.log(result.data);
                }
            }
            catch (error) {
                console.log("finish error", error)
                setPageStatus("error")
            }
        }
        fetchData()
    }, [model])
    const [departmant, setDepartmant] = useState({})
    const [pageStatus, setPageStatus] = useState("load")

    function createMarkup() {
        return {__html: departmant.f_description};
    }

    switch (pageStatus) {
        case "error":
            return (
                <React.Fragment>
                    <div className="container">
                        <CustomNavbar nameClass={navColor}/>
                    </div>
                    <div className=" mr-5 my-5">
                        {/*style={{marginRight: "30px"}}*/}
                        <h2 className="text-fosfori">
                            تیتر
                        </h2>
                        <h1>
                            {departmant.f_title}
                        </h1>
                    </div>
                    <div className="l-container" style={{background: "#F7F7F7"}}>
                        <div className="d-flex justify-content-center">
                            <h1>
                                دپارتمان پیدا نشد
                            </h1>
                        </div>
                    </div>
                    <Footer/>
                </React.Fragment>
            )
        case "successful":
            return (
                <React.Fragment>
                    <div className="container">
                        <CustomNavbar nameClass={navColor}/>
                    </div>
                    <div className="container mr-5 my-5">
                        <h2 className="text-fosfori">
                            تیتر
                        </h2>
                        <h1>
                            {departmant.f_title}
                        </h1>
                    </div>
                    <div className="l-container" style={{background: "#F7F7F7"}}>
                        <div className="container">
                            <div className="mr-4 row">
                                <div className="col-lg-6">
                                    <h2>شرح خدمات</h2>
                                    <div style={{fontSize: "15px"}}>
                                        <span dangerouslySetInnerHTML={createMarkup()}/>
                                    </div>
                                </div>
                                <div className="col-lg-6 bg-white py-4">
                                    <div className="row">
                                        <div className="col-4">
                                            <img src={departmant.f_responsible_image} alt="pic"
                                                 className="img-responsive"/>
                                        </div>
                                        <div className="col">
                                            <h5>
                                                مسئول دپارتمان
                                            </h5>
                                            <h1 className="text-fosfori">
                                                {departmant.f_responsible_fullname}
                                            </h1>
                                            {departmant.f_social_network && departmant.f_social_network.map(
                                                (s_net, index) => {
                                                    return (
                                                        <a href={s_net.f_link} key={index}>
                                                            <img src={s_net.f_file} alt={s_net.f_title}/>
                                                        </a>
                                                    )
                                                }
                                            )
                                            }
                                        </div>
                                    </div>
                                    <h5 className="my-3">سوابق تحصیلی</h5>
                                    <div className="row">
                                        <div className="col-3 px-2">
                                            <div className="d-flex flex-column">
                                                <div className="p-2 text-center" style={{backgroundColor: "#F3F3F3",}}>
                                                    مدرک تحصیلی
                                                </div>
                                                <div
                                                    className="p-2 text-center">{departmant.f_responsible_resume_eduction_degree}</div>
                                            </div>
                                        </div>
                                        <div className="col-3 px-2">
                                            <div className="d-flex flex-column">
                                                <div className="p-2 text-center" style={{backgroundColor: "#F3F3F3",}}>
                                                    رشته تحصیلی
                                                </div>
                                                <div
                                                    className="p-2 text-center">{departmant.f_responsible_resume_eduction_field}</div>
                                            </div>
                                        </div>
                                        <div className="col-3 px-2">
                                            <div className="d-flex flex-column">
                                                <div className="p-2 text-center" style={{backgroundColor: "#F3F3F3",}}>
                                                    دانشگاه محل تحصیل
                                                </div>
                                                <div
                                                    className="p-2 text-center">{departmant.f_responsible_resume_eduction_university}</div>
                                            </div>
                                        </div>
                                        <div className="col-3 px-2">
                                            <div className="d-flex flex-column">
                                                <div className="p-2 text-center" style={{backgroundColor: "#F3F3F3",}}>
                                                    تاریخ فارغ التحصیلی
                                                </div>
                                                <div
                                                    className="p-2 text-center">{departmant.f_responsible_resume_eduction_date}</div>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="my-3">
                                        {departmant.f_responsible_resume_experience}
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <Footer/>
                </React.Fragment>
            );
        default:
            return <Wait/>;
    }


}
export default Departmant;