import React, {useEffect, useState} from "react"
import {
    useParams
} from "react-router-dom";
import axios from "../apis/axios";
import InputRender from "./InputRender";
import urlTableFields from "../Constant/urlTableFields";
import Wait from "./Wait";
import urlTable from "../Constant/urlTable";


const Edit = () => {
    const [pageStatus, setPageStatus] = useState("");
    const [modelFields, setModelFields] = useState({});
    const [formValues, setFormValues] = useState({});
    const [pageForm, setPageForm] = useState({});
    const {model, id} = useParams();


    useEffect(() => {
        const fetchData = async () => {
            try {
                const result = await axios.get(urlTableFields[model]);
                if (result.status === 200) {
                    setModelFields(result.data.fields);
                }
            } catch (error) {
                setPageStatus("error")
            }
            try {
                const result = await axios.get(`${urlTable[model]}/${id}`);
                if (result.status === 200) {
                    setFormValues(result.data);
                    setPageStatus("successful");

                }
            } catch (error) {
                setPageStatus("error")
            }
        }
        fetchData();
    }, [model,id])


    const onFormSubmit = (e) => {
        const postIt = async (e) => {
            e.preventDefault();
            console.log("before post", formValues)
            setPageStatus("loading")
            console.log(formValues);
            const formData = new FormData()
            Object.keys(formValues).forEach(
                (key) => {
                    if ((typeof formValues[key]) === "string") {
                        formData.append(key, formValues[key])
                    } else if (key === "f_file") {
                        formData.append(key, formValues[key])
                    } else if ((typeof formValues[key]) === "object") {
                        console.log("when is object", formValues[key], key)
                        for (const item of formValues[key]) {
                            formData.append(key, item.value)
                        }
                    }
                }
            )
            const config = {
                headers: {
                    'Content-Type': 'multipart/form-data;'
                }
            }
            try {
                let result = await axios.patch(`${urlTable[model]}/${id}/`, formData, config);
                if (result.status === 200) {
                    alert("اطلاعات با موفقیت ثبت شد")
                    pageForm.reset();
                    setPageStatus("successful")
                }
            } catch (error) {
                alert("در فرآیند ثبت اطلاعات مشکل بوجود آمده است");
                setPageStatus("error");
            }
        }
        postIt(e);
    }
    if (pageStatus === "successful") {
        return (
            <React.Fragment>
                <form method="POST" onSubmit={onFormSubmit} ref={form => setPageForm(form)}>
                    {
                        Object.keys(modelFields).map((key, index) => {
                                return (
                                    <div className="m-3 mx-5" key={index}>
                                        <InputRender
                                            type={modelFields[key].type[0]}
                                            id={key}
                                            choices={modelFields[key].choices}
                                            setFormValues={setFormValues}
                                            formValues={formValues}
                                        />
                                    </div>
                                )
                            }
                        )
                    }
                    <button type="submit" className="btn btn-primary">Submit</button>
                </form>
            </React.Fragment>
        )
    } else if (pageStatus === "error") {
        return (
            <React.Fragment>
                <h1>
                    Server Error
                </h1>
            </React.Fragment>
        )
    } else if (pageStatus === "loading") {
        return (
            <React.Fragment>
                <Wait pageError={""}/>
            </React.Fragment>
        )
    } else {
        return (
            <React.Fragment>
                <h1>
                    Error
                </h1>
            </React.Fragment>
        )
    }

}
export default Edit;