import React, {useEffect, useState} from "react"
import {
    useParams
} from "react-router-dom";
import axios from "../apis/axios";
import urlTableFields from "../Constant/urlTableFields";
import urlTableData from "../Constant/urlTableData";
import Wait from "./Wait";
import InputRenderAdd from "./InputRenderAdd";


const Add = () => {
    const [pageStatus, setPageStatus] = useState("");
    const [modelFields, setModelFields] = useState({});
    const [formFields, setFormFields] = useState({});
    const [pageForm, setPageForm] = useState({});
    const {model} = useParams();


    useEffect(() => {
        const fetchData = async () => {
            try {
                const result = await axios.get(urlTableFields[model]);
                if (result.status === 200) {
                    setModelFields(result.data.fields);
                    setPageStatus("successful");
                }
            } catch (error) {
                setPageStatus("error")
            }
        }
        fetchData()
    }, [model])

    // const inputValueSave = (value) => {
    //     setFormFields({...formFields, ...value});
    //     // console.log(formFields);
    // }
    const onFormSubmit = (e) => {
        const postIt = async (e) => {
            e.preventDefault();
            console.log("formFields before submit", formFields);
            setPageStatus("loading")
            const formData = new FormData()
            Object.keys(formFields).forEach(
                (key) => {
                    if ((typeof formFields[key]) === "string") {
                        formData.append(key, formFields[key])
                    } else if (key === "f_file") {
                        formData.append(key, formFields[key])
                    } else if ((typeof formFields[key]) === "object") {
                        console.log("when is object", formFields[key] , key)
                        formFields[key].forEach((item) => {
                                formData.append(key, item.value)
                            }
                        )
                    }
                }
            )
            console.log("form data after set", formData)
            const config = {
                headers: {
                    'Content-Type': 'multipart/form-data;'
                }
            }
            try {
                let result = await axios.post(urlTableData[model], formData, config);
                if (result.status === 201) {
                    alert("اطلاعات با موفقیت ثبت شد")
                    pageForm.reset();
                    setPageStatus("successful")
                }
            } catch (error) {
                alert("در فرآیند ثبت اطلاعات مشکل بوجود آمده است")
                setPageStatus("error")
            }
        }
        postIt(e);
    }
    if (pageStatus === "successful") {
        return (
            <React.Fragment>
                <form method="POST" onSubmit={onFormSubmit} ref={form => setPageForm(form)}>
                    {
                        Object.keys(modelFields).map((key, index) => {
                                return (
                                    <div className="m-3 mx-5" key={index}>
                                        <InputRenderAdd
                                            type={modelFields[key].type[0]}
                                            id={key}
                                            choices={modelFields[key].choices}
                                            setFormFields={setFormFields}
                                        />
                                    </div>
                                )
                            }
                        )
                    }
                    <button type="submit" className="btn btn-primary">Submit</button>
                </form>
            </React.Fragment>
        )
    } else if (pageStatus === "error") {
        return (
            <React.Fragment>
                <h1>
                    Server Error
                </h1>
            </React.Fragment>
        )
    } else if (pageStatus === "loading") {
        return (
            <React.Fragment>
                <Wait pageError={""}/>
            </React.Fragment>
        )
    } else {
        return (
            <React.Fragment>
                <h1>
                    Error
                </h1>
            </React.Fragment>
        )
    }

}
export default Add;