import React, {useEffect, useState} from "react"
import { useHistory } from "react-router-dom";

import {
    useParams
} from "react-router-dom";
import axios from "../apis/axios";
import urlTable from "../Constant/urlTable";

const Delete = ({prefix}) => {
    const history = useHistory()

    const [pageStatus, setPageStatus] = useState("");

    const {model, id} = useParams();
    useEffect(() => {
        const fetchData = async () => {
            try {
                const result = await axios.delete(`${urlTable[model]}/${id}`);
                if (result.status === 204) {
                            setPageStatus("successful");
                        }
            } catch (error) {
                setPageStatus("error")
            }
        }
        fetchData()
    }, [model,id])

    if (pageStatus === "successful") {
        return (
            <React.Fragment>
                <h1>
                    {model} {id} Deleted successfully
                    {alert(`${model} ${id} Deleted successfully`)}
                    {history.push(`${prefix}/list/${model}`)}
                </h1>
            </React.Fragment>
        )
    } else if (pageStatus === "error") {
        return (
            <React.Fragment>
                <h1>
                    Server Error
                </h1>
            </React.Fragment>
        )
    }else {
        return (
            <React.Fragment>
                <h1>

                </h1>
            </React.Fragment>
        )
    }

}
export default Delete;